    height:100vh;
    background-color: azure;
    margin: 0px;
   

}
.brand_name{
    justify-content: center;
    margin-top: 100px;
    align-items: center;
    text-align: center;
font-size: 50px;
color: gray;
margin-bottom: 20px;
